@keyframes slideInFromBottom {
    0% {
        transform: translateY(100%);
    }
    100% {
        transform: translateY(0);
    }
}

.delayedPopupWindow {
    border-radius: 1rem;
    left: 0;
    right: 0;
    position: fixed;
    background-color: $mint;
    color: $twilight;
    z-index: 3;
    padding: $block4;
    margin: $block4;
    font-size: 0.8rem;
    opacity: 0.98;
    font-family: "Roboto Mono", monospace;
    line-height: 0;
    height: auto;
    animation: 0.3s ease-out 0s 1 slideInFromBottom;
    text-transform: uppercase;
    bottom: 0;
    display: none;
    align-items: center;
}

.popup {
    display: flex;
    width: 100%;
}

.formDescription {
    width: 100%;
    margin: auto;
    overflow-wrap: break-word;
    margin-left: $block2;
}


/*   This is the closing button  */

#btnClose {
    margin: auto;
    svg {
        width: 16px;
        stroke-width: 15;
        stroke-linecap: round;
        stroke-miterlimit: 10;
        fill: #ffffff;
        stroke: $twilight;
        right: $block4;
    }
}